
import { defineComponent } from 'vue';
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import type {SubUserList,CIB20200311_REQ_BODY,CIB20200311_RES} from "@/shared/type/CIB20200311.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";

export default defineComponent({
  name: 'cib202003sc00',
  components: {
    Pagination,
    BreadCrumb
  },
  data(){
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    const customerName = this.$route.query.customerName;
    const managerBankID = this.$route.query.managerBankID;
    const cellPhoneNo = this.$format.formatPhoneNumber(this.$route.query.cellPhoneNo);
    const companyName = this.$route.query.companyName;
    const masterUserStatus = this.$route.query.userStatus;
    const mainUserRoleID = this.$route.query.mainUserRoleID;
    const headers= [
      { columnName: this.$t("cib202003sc00.no") },
      { columnName: this.$t("cib202003sc00.view") },
      { columnName: this.$t("cib202003sc00.user_id") },
      { columnName: this.$t("cib202003sc00.user_name") },
      { columnName: this.$t("cib202003sc00.role") },
      { columnName: this.$t("cib202003sc00.cell_phone_no") },
      { columnName: this.$t("cib202003sc00.user_status") }
    ];
    return{
      headers,
      subUserLists: [] as SubUserList[],
      customerInfo:{
        userID:"",
        customerNo:"",
        customerName:"",
        masterUserID:"",
        cellPhoneNo:"",
        managerBankID:""
      },
      userID,
      customerNo,
      customerName,
      managerBankID,
      cellPhoneNo,
      companyName,
      masterUserStatus,
      mainUserRoleID,
      isDisableInput:true,
      isDisableRegister:false,
      pageNumber:1,
      pageSize: PAGE_SIZE,
      startingNo:1,
      totalUser: 0
    }
  },

  mounted(){
    this.getSubUserLists();
    this.handleCheckMasterUserStatus();
  },

  
  computed: {
    userStatusCode(){
      return (serviceStatusCode: string ,serviceStatusName: string) => `${serviceStatusCode}` + ' : ' + `${serviceStatusName}`;
    }
  },

  methods: {
  async getSubUserLists(){
      const reqBody:CIB20200311_REQ_BODY = {
        customerNo: `${this.customerNo}`,
        managerBankID:  `${this.managerBankID}`,
        pageSize: this.pageSize,
        pageNumber: this.pageNumber
      };
       
      this.customerInfo.customerNo = `${this.customerNo}`
      this.customerInfo.customerName = `${this.customerName}`;
      this.customerInfo.masterUserID = `${this.managerBankID}`;
      this.customerInfo.cellPhoneNo = `${this.cellPhoneNo}`;
 
      const res = await this.$request.getRequest( "CIB20200311",reqBody, false ) as CIB20200311_RES
 
      const resData = res?.body.list.map((item:SubUserList,index:number)=>{
        item["No"] = index + this.startingNo;
        item["User ID"] = item.userID;
        item["User Name"] = `${item.firstName +' '+ item.lastName}`;
        item["Role"] = item.userRoleIDName;
        item["Cell Phone No"] =  this.$format.formatPhoneNumber(item.cellPhoneNo);
        item["User Status"] = this.userStatusCode(item.serviceStatusCode, this.$codeConverter.convertValue("ServiceStatusCode", item.serviceStatusCode))
        return item;
      });

      this.totalUser = res.body.totalCount;
      this.subUserLists = resData;
    },

    backToCompanyList(){
      this.$router.push('CIB202002SC00');
    },

    handleCheckMasterUserStatus(){
      // if master user status = '00' or '01' allow he/she register sub user.
      if((this.masterUserStatus ==  '00' && this.mainUserRoleID == 'STP') || (this.masterUserStatus == '01' && this.mainUserRoleID == 'STP') || (this.masterUserStatus == '00' && this.mainUserRoleID != 'STP') || (this.masterUserStatus == '01' && this.mainUserRoleID != 'STP')){
        this.isDisableRegister = false
      }else{
        this.isDisableRegister = true
      }
    },

    moveToRegister(){
      this.$router.push({
        path:`CIB202003SC01`,
        query:{
          type:'register',
          userID: this.userID,
          customerNo: this.customerNo,
          customerName: this.customerName,
          managerBankID: this.managerBankID,
          companyName: this.companyName,
          userStatus: this.masterUserStatus,
          mainUserRoleID: this.mainUserRoleID
        }
      });
    },

    moveToEditRegister(index:number){
      this.$router.push({
        path:`CIB202003SC01`,
        query:{
          type:"edit",
          customerNo: this.subUserLists[index].customerNo,
          customerName: this.customerName,
          userID: this.subUserLists[index].userID,
          managerBankID: this.subUserLists[index].managerBankID,
          cellPhoneNo: this.cellPhoneNo,
          userStatus: this.masterUserStatus,
          mainUserRoleID: this.mainUserRoleID
        }
      });
    },

    handlePagination(pageNumber: number){
      this.pageNumber = pageNumber;
      this.getSubUserLists();
    },
  
    handleStartingIndex(startingIndex: number){
      this.startingNo = startingIndex;
    }
  }
})
